import React from "react";
import { Card } from 'antd';

const { Meta } = Card;

const Factcards=()=>{
    return(
        <>
        <div className="row ">
        <Card
        className="col-md-3"
            hoverable
            style={{ width: 240 }}
            cover={<img alt="example" src="https://familydoctor.org/wp-content/uploads/2010/05/shutterstock_300553067.jpg" />}
        >
            <Meta title="Eat Healthy and Get Active" description="Learn about the benefits of good nutrition, regular physical activity, and staying at a healthy weight." />
        </Card>

        <Card
        className="col-md-3"
            hoverable
            style={{ width: 240 }}
            cover={<img alt="example" src="https://i0.wp.com/cdn-prod.medicalnewstoday.com/content/images/articles/319/319460/quitting-smoking-can-be-tough-but-we-have-put-together-some-steps-that-may-help-you-along-the-way.jpg?w=1155" />}
        >
            <Meta title="Stay Away From Tobacco" description="
By quitting smoking, you can: Lengthen your life expectancy. Decrease your risk of disease." />
        </Card>

        <Card
        className="col-md-3"
            hoverable
            style={{ width: 240 }}
            cover={<img alt="example" src="https://www.cancer.org/content/dam/cancer-org/images/homepage/hpv.png/jcr:content/renditions/cq5dam.thumbnail.350.350.jpeg" />}
        >
            <Meta title="HPV Prevention" description="Human Papillomavirus can cause 6 types of cancer. But preventing it — and the cancers it causes — is simple." />
        </Card>

        <Card
        className="col-md-3"
            hoverable
            style={{ width: 240 }}
            cover={<img alt="example" src="data:image/jpeg;base64," />}
        >
            <Meta title="Get Screened" description="Learn about recommended cancer screening tests and find resources to help you get on a screening schedule that's right for you." />
        </Card>
        </div>

        </>
        
    )
}

export default Factcards;